html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Palanquin", sans-serif;
  margin: 0;
  color: #000;
}

h1 {
  font-size: 2.5rem;
}
@media (min-width: 480px) {
  h1 {
    font-size: 3rem;
  }
}
@media (min-width: 768px) {
  h1 {
    font-size: 3.5rem;
  }
}

h2 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  h2 {
    font-size: 2.5rem;
  }
}

h3 {
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  h3 {
    font-size: 2rem;
  }
}

p {
  text-align: left;
}

p, a, blockquote, input[type=text], input[type=tel], input[type=number], input[type=email], input[type=submit], label {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 1.8rem;
}

a h1 {
  text-decoration: none;
}
a:link, a:visited, a:active {
  text-decoration: none;
}

.enlace {
  color: #00313E;
}

.contenedor, .contenido-centrado {
  margin: 0 auto;
  max-width: 120rem;
  width: 90%;
}

.fondo-oscuro {
  background-color: #e1e1e1;
}

.mb-3 {
  margin-bottom: 3rem;
}

.contenido-centrado {
  max-width: 70rem;
  width: 90%;
  text-align: center;
}
@media (min-width: 768px) {
  .contenido-centrado {
    width: 30%;
  }
}

.btn-azul-claro {
  border-radius: 2rem;
  background-color: #1884A5;
  color: #FFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 2rem;
  text-align: center;
  display: inline-block;
  border: none;
  font-size: 1.5rem;
}
.btn-azul-claro:hover {
  background-color: #15728f;
  cursor: pointer;
}

/*UI*/
.hero {
  background-image: url("/build/img/fondo-hero.svg");
  height: 60rem;
  background-position: 500rem;
  background-size: cover;
  color: #FFF;
  padding-top: 10rem;
}
.hero .titulo-hero, .hero p {
  color: #FFF;
}
@media (min-width: 768px) {
  .hero {
    background-position: center center;
    background-size: cover;
    height: 90rem;
  }
}

.titulo-header {
  color: #FFF;
}
.titulo-header.titulo-inicio {
  color: #000;
}

.navbar-contenedor {
  background-color: #00313E;
  color: #FFF;
}
.navbar-contenedor.inicio {
  background-color: #FFF;
  color: #00313E;
}
.navbar-contenedor.page-404 {
  height: 36.3rem;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 7rem;
}

.caracteristicas {
  margin-top: 7rem;
}

.caracteristica {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 2fr 3fr;
  gap: 2rem;
}
@media (min-width: 768px) {
  .caracteristica {
    grid-template-columns: 1fr 3fr;
    gap: 7rem;
  }
}

.testimoniales {
  margin-top: 7rem;
}

.contenedor-testimoniales {
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .contenedor-testimoniales {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.contenedor-testimoniales .testimonial {
  background-color: #00313E;
  color: #FFF;
  padding: 1rem 1rem 1rem 5rem;
  border-radius: 2rem;
  margin-top: 3rem;
}

blockquote {
  position: relative;
}
blockquote .cita::before {
  content: "“";
  position: absolute;
  font-size: 10rem;
  left: -6rem;
  top: -1rem;
  font-family: "PT Sans", sans-serif;
}

footer {
  background-color: #00313E;
  padding: 2rem 0;
  height: 7rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
footer p {
  text-align: center;
  color: #FFF;
  margin: 0;
}

/*Internas*/
.formulario {
  margin-top: 3rem;
}
.formulario .campo {
  margin: 2rem 0;
}
.formulario .campo:last-of-type {
  margin-bottom: 0;
}
.formulario .campo label {
  display: block;
  margin-bottom: 1rem;
  text-align: left;
}
.formulario .campo input[type=text], .formulario .campo input[type=tel], .formulario .campo textarea {
  border: 1px solid #707070;
  background-color: #efefef;
  padding: 0.8rem;
  border-radius: 1rem;
  height: 4rem;
}
.formulario .campo input, .formulario .campo label {
  display: block;
  font-size: 1.8rem;
  width: 100%;
}
.formulario .campo input[type=submit] {
  background-color: #00313E;
  color: #FFF;
  padding: 1rem;
  border: none;
  border-radius: 1rem;
}
.formulario .form-opciones {
  display: flex;
  justify-content: space-between;
}

fieldset {
  margin-bottom: 3rem;
  padding: 0 1.5rem 1.5rem 1.5rem;
}
fieldset legend {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: left;
  font-size: 2rem;
}

.page-404 {
  margin-top: 10rem;
  text-align: center;
}
.page-404 img {
  max-width: 70%;
}
@media (min-width: 768px) {
  .page-404 img {
    max-width: 40%;
